.Link {
  min-width: 10rem;
  display: block;
  text-align: center;
  color: #fff;
  padding: 1rem 0;
  border-radius: 2rem;
  margin: 1rem 0;
  font-size: .8rem;
  text-transform: uppercase;
  font-weight: bold;
}

.Link:hover {
  filter: brightness(120%);
}

.Link-primary {
  background-color: rgb(255, 90, 95);
}

.Link-secondary {
  background-color: #fff;
  color: rgb(255, 90, 95);
}