.Popout {
  position: relative;
  z-index: 2;
}
.Popout-left {
  left: 3.5rem;
}

.Popout-right {
  right: 3.5rem;
}

.Popout-tooltip {
  position: absolute;
  max-width: 20rem;
  background-color: #000;
  color: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  opacity: 0;
  transition: all 0.3s;
  z-index: -1;
}

.Popout-tooltip::after {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.Popout-left .Popout-tooltip::after {
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-right: 15px solid #000;
  top: 45%;
  left: -2.5%;
}

.Popout-right .Popout-tooltip::after {
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  border-left: 15px solid #000;
  top: 45%;
  right: -2.5%;
}

.Popout.is-expanded .Popout-tooltip {
  opacity: 1;
  transition-delay: 0.3s;
  z-index: 2;
}

.Popout.Popout-left.is-expanded .Popout-button {
  transform: rotate(90deg) translateY(-20%);
  transition-delay: 0.3s;
}

.Popout.Popout-right.is-expanded .Popout-button {
  transform: rotate(270deg) translateY(-20%);
  transition-delay: 0.3s;
}

.Popout-button {
  transition: all 0.3s;
  position: absolute;
}

.Popout-left .Popout-tooltip {
  left: 3.5rem;
}

.Popout-right .Popout-tooltip {
  right: 1.5rem;
}

.Popout-left .Popout-button {
  left: -7rem;
  transform: rotate(90deg);
}

.Popout-right .Popout-button {
  right: -7rem;
  transform: rotate(270deg);
}

@media screen and (max-width: 720px) {
  .Popout-left .Popout-button {
    transform: rotate(90deg) scale(.75);
  }
  .Popout-right .Popout-button {
    transform: rotate(270deg) scale(.75);
  }
  .Popout.Popout-left.is-expanded .Popout-button {
    transform: rotate(90deg) scale(.75) translateY(-20%);
    transition-delay: 0.3s;
  }
  .Popout.Popout-right.is-expanded .Popout-button {
    transform: rotate(270deg) scale(.75) translateY(-20%);
    transition-delay: 0.3s;
  }
  .Popout-tooltip {
    max-width: 200px;
  }
}