.Header {
  position: sticky;
  top: 0;
  background-color: rgba(255, 255, 255, 0.97);
  border-bottom: 1px solid #ccc;
  z-index: 999;
  display: flex;
  align-items: center;
  padding: 0 15%;
}

.Header ul {
  margin-left: auto;
}

.Header-link {
  display: inline-block;
  padding: 0 20px;
}

.Header a {
  color: rgb(34, 34, 34);
}

.Header a:hover {
  color: #a9c8b6;
}

@media screen and (max-width: 1024px) {
  .Logo {
    width: 100px;
  }
}