.Event-time,
.Event-title,
.Event-location {
  font-weight: bold;
  text-align: left;
}

.Event-time {
  position: absolute;
  left: -8rem;
}

.Event-circle::before {
  content: "";
  width: 11px;
  height: 11px;
  border-radius: 50%;
  border: 2px solid #666;
  position: absolute;
  left: -3rem;
  z-index: 1;
  background-color: #fff;
}

.Event-title {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.Event-location {
  margin-bottom: 1.5rem;
}

.Event-description {
  margin-bottom: 2.5rem;
  color: #666;
}

.Event {
  position: relative;
  margin-bottom: 2rem;
}

.Events-timeline {
  position: relative;
}

.Events-timeline::before {
  content: "";
  width: 5px;
  height: 100%;
  background-color: #666;
  position: absolute;
  top: 0;
  left: -43px;
}

.Events {
  max-width: 800px;
  margin: 0 auto;
}

@media screen and (max-width: 1024px) {
  .Events {
    max-width: 600px;
  }
}

@media screen and (max-width: 720px) {
  .Events {
    max-width: 240px;
  }
  .Event-time {
    position: inherit;
    left: 0;
    margin-bottom: 1rem;
  }
    .Event-circle::before {
      top: 0;
    }
}