.Accommodations-hotel,
.Accommodations-airbnb {
  position: relative;
  text-align: center;
}

.Accommodations-hotel img,
.Accommodations-airbnb img {
  width: 100%;
  opacity: 70%;
}

.Accommodations-hotel img {
  filter: brightness(50%);
}

.Accommodations-airbnb-text,
.Accommodations-hotel-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.Accommodations-hotel-phone {
  font-size: .8rem;
  color: #a9c8b6;
  font-weight: bold;
}

.Accommodations p {
  color: #fff;
  font-size: .8rem;
  margin-top: .5rem;
}

@media screen and (min-width: 1024px) {
  .Accommodations-hotel,
  .Accommodations-airbnb {
    width: 50%;
    display: inline-block;
  }
}

@media screen and (max-width: 720px) {
  .Accommodations-hotel img,
  .Accommodations-airbnb img {
    width: auto;
    height: 100%;
  }
  .Accommodations-hotel-text {
    width: 90%;
  }
}