.MainCTA {
  position: relative;
}

.MainCTA img {
  max-width: 100%;
  filter: brightness(50%) saturate(140%);
}

.MainCTA-content {
  color: white;
  position: absolute;
  text-align: center;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.MainCTA-content h1 {
  white-space: nowrap;
}

.MainCTA-content h1,
.MainCTA-content span {
  font-size: 6rem;
  font-family: "Amatic SC", cursive;
}

.MainCTA-content h1,
.MainCTA-content h2,
.MainCTA-content span {
  font-family: "Amatic SC", cursive;
}

.MainCTA-content h2 {
  font-weight: 400;
  font-size: 2.5rem;
}
@media screen and (max-width: 720px) {
  .MainCTA-content {
    top: 50%;
  }
  .MainCTA-content h1 {
    font-size: 3rem;
  }
  .MainCTA-content h2 {
    font-family: "Open Sans";
    margin-top: 16px;
    font-size: 1rem;
  }
}